import './styles.css';

class CountdownTimer {
  constructor(selector, targetDate) {
    this.selector = selector;
    this.targetDate = targetDate;
  }
}

const bodyEl = document.querySelector('body');
// const daysElements = document.querySelector('[data-value="days"]');
// const hoursElements = document.querySelector('[data-value="hours"]');
// const minsElements = document.querySelector('[data-value="mins"]');
// const secElements = document.querySelector('[data-value="secs"]');

let currentTime;
let time;
let timer = new CountdownTimer('#timer-1', new Date('Jul 13, 2021'));
let days;
let hours;
let mins;
let secs;

const updateTime = () => {
  currentTime = new Date();
  time = timer.targetDate - currentTime;
  days = Math.floor(time / (1000 * 60 * 60 * 24));
  hours = Math.floor((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  mins = Math.floor((time % (1000 * 60 * 60)) / (1000 * 60));
  secs = Math.floor((time % (1000 * 60)) / 1000);
};
updateTime();

const showTimer = () => {
  updateTime();
  bodyEl.innerHTML = ` <div class="timer" id="timer-1">
      <div class="field">
        <span class="value" data-value="days">${days}</span>
        <span class="label">Days</span>
      </div>

      <div class="field">
        <span class="value" data-value="hours">${hours}</span>
        <span class="label">Hours</span>
      </div>

      <div class="field">
        <span class="value" data-value="mins">${mins}</span>
        <span class="label">Minutes</span>
      </div>

      <div class="field">
        <span class="value" data-value="secs">${secs}</span>
        <span class="label">Seconds</span>
      </div>
    </div>`;
};

const timerId = setInterval(showTimer, 1000);
